/*eslint-disable*/
import React, { useState } from 'react';
import './App.css';
import background from './main.gif';
import main from './artist.jpg'
import { Link, Route, Switch} from 'react-router-dom';
import Artworks from './view/Artworks.js';
import Projects from './view/Projects.js';
import Contact from './view/Contact.js'
import LoveAlgorithm from './view/LoveAlgorithm' 
import About from './view/About'
import CV from './view/CV'
import styled from "styled-components"
import Interstyle from './subpages/projects/Interstyle.js'; 
import NatureMU from './subpages/artworks/NatureMU.js'; 

const Block = styled.header`
display: flex;
width: 1000px;
height: 350px;
flex: 1;
flex-direction: row;
flex-wrap: wrap;
font-family: Comfortaa;
justify-content: space-around;
align-items: center;
backgroundImage: url(${background});
`;

function App() {
  let [love, changeLove] = useState(0);
  let [click,clickChange] = useState(0);
  let [menu,changeMenu] = useState(['Home','About','Artworks','Work Experience','CV','Contact']);
  let [modal,changeModal] = useState(false);
  
    return (
    <div className="App">
      <div className="black-background">
        <div className="love d-flex align-items-center">
          <img className="d-flex" src={background} style={{width:'100%'}}/>
        </div>
      </div>
      <Route exact path="/">
        <div className="black-nav">
          <div>JINHA KANG</div>
        </div>
        <div className = "menu">
          <Link to ="/" style={{textDecoration: "none", color: "ghostwhite" , top:"100px"}}><h4>{menu[0]}</h4></Link>
          <Link to ="/about" style={{textDecoration: "none", color: "ghostwhite"}}><h4>{menu[1]}</h4></Link>
          <Link to ="/artworks" style={{textDecoration: "none", color: "ghostwhite"}}><h4>{menu[2]}</h4></Link>
          <Link to ="/Projects" style={{textDecoration: "none", color: "ghostwhite"}}><h4>{menu[3]}</h4></Link>
          <Link to ="/CV" style={{textDecoration: "none", color: "ghostwhite"}}><h4>{menu[4]}</h4></Link>
          <Link to ="/Contact" style={{textDecoration: "none", color: "ghostwhite"}}><h4>{menu[5]}</h4></Link>
        </div>
        <div className="list">
          
          {/* <button  onClick={ ()=>{changeModal(!modal)}}> click</button>
         {
          modal === true
          ? <Modal></Modal>
          : null 
        } */}
          {/* <hr/> */}
        </div>

         {/* <Modal title={title}></Modal> */}
      </Route>

      <Route exact path="/about">
      <About />
      </Route>

      <Route exact path="/artworks">
      <Artworks />
      </Route>

     <Route exact path="/Projects">
      <Projects />
      </Route>

      <Route exact path="/CV">
      <CV />
      </Route>

      <Route exact path="/Contact">
        <Contact />
      </Route>

      <Route exact path="/Interstyle">
        <Interstyle />
      </Route>

      <Route exact path="/loveAlgorithm">
       <LoveAlgorithm />
      </Route>

      <Route exact path="/NatureMU">
        <NatureMU />
      </Route>

     </div>
  );
}

export default App;