import React, { useState } from 'react';
import ImageBox from '../component/ImageBox';
import TextBox from '../component/TextBox';
import styled from "styled-components"
import useWindowDimensions from '../component/Viewport'
import Interstyle from '../subpages/projects/Interstyle'
import { Link, Route, Switch} from 'react-router-dom';

const Block = styled.div`
display: flex;
flex: 1;
flex-direction: row wrap;
flex-wrap: wrap;
width: 100%;
background-color: black;
font-family: Comfortaa;
justify-content: space-around;
align-items: center;
margin-bottom: 50px;
flex-shrink: 0;
color : white;
`;

const FirstBlock = styled(Block)`
height: 350px;
`

const SecondBlock = styled(Block)`
height: 250px;
`

const firstText = "Interstyle Color LAB\n Vancouver, Canada \n Design Developer\n 2018\n \n \n"
const secondText = "Straight Line Designs\n Vancouver, Canada \n 3D Moderler & Artist Assistant: Judson Beaumont \n 2018"
const thirdText = "WhyDots\n Seoul, Korea \n 3D Modeler and Inventor \n 2017"

function Projects(){
  const {height, width} = useWindowDimensions();
  console.log("width is " + width)
    return(
      <Block id="Projects" width="100%">
       
 <TextBox text="Projects" 
        width="80%"
        fontSize={width/30 + "px"}
        font-family="Comfortaa"
        fontColor = "white"
        font-align = "right"
        />
       
         <ImageBox img = "https://user-images.githubusercontent.com/75330517/161396500-deab444e-9f40-4895-9c77-354c33b557be.jpg" 
          />
         <TextBox text=<Link to ="/Interstyle" style={{width:"50%", textDecoration:'none',color:'white'}}>{firstText}</Link>/>
       
         <TextBox text={secondText}/>
        <ImageBox img = "https://user-images.githubusercontent.com/75330517/161367247-c5e4654f-c438-40d6-a44d-98c912b96b25.jpg"
        />

        <ImageBox img = "https://user-images.githubusercontent.com/75330517/161367404-f4d4f21e-f0bd-4379-a41f-3cc000924476.jpg"
        />
        <TextBox text={thirdText}/>

      </Block>
    )
  }

  export default Projects;